import { Routes, RouterModule } from '@angular/router';
import { PacientetComponent } from './pacientet/pacientet.component';
import { LoginComponent } from './login/login.component';
import { RecetaComponent } from './receta/receta.component';
import { PA_ROUTES } from './pacientet/child_pacient.routes';
import { RE_ROUTES } from './receta/child_receta.routers';

// ROUTER KRYESOR
const APP_ROUTES: Routes = [
    { path: '', redirectTo: 'login', pathMatch: 'full' },
    { path: 'pacientet', component: PacientetComponent, children: PA_ROUTES},
    { path: 'recetat', component: RecetaComponent},
    { path: 'login', component: LoginComponent }

   
  ];
export const routing = RouterModule.forRoot (APP_ROUTES);



