import { Injectable } from '@angular/core';

@Injectable()
export class HeaderService {

  visible: boolean;

  constructor() { this.visible = true; }

  hide() { this.visible = true; }

  show() { this.visible = true; }

  toggle() { this.visible = !this.visible; }

  doSomethingElseUseful() { }

}



