import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { PacientetService } from 'app/pacientet/pacientet.service';
//import { Pacient } from 'app/pacientet/pacient';
import { DataSource } from '@angular/cdk/collections';
import { Receta } from 'app/receta/receta';
import { RecetaService } from 'app/receta/receta.service';
import { Router } from '@angular/router';
//import { Pacient } from 'app/pacientet/pacient';




@Component({
  selector: 'app-lista-pacient-mat',
  templateUrl: './lista-pacient-mat.component.html',
  styleUrls: ['./lista-pacient-mat.component.css']
})

export class ListaPacientMatComponent {
  @Input() selectedPacient: Pacient = null;
  @Output() pacientSelected = new EventEmitter<Pacient>();
  pacients: Pacient[] = [];
  //@Input() pacient: Pacient;
  displayedColumns = ['emri_pacientit', 'rraca', 'data', 'age', 'emri_pronarit', 'shenime'];
  displayedData: MatTableDataSource<Pacient>;

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.displayedData.filter = filterValue;
  }

  constructor(private ps: PacientetService, private rs: RecetaService, private router: Router, ) {
    this.pacients = this.ps.getPacients()
    //this.ps.fetchData();
    this.displayedData = new MatTableDataSource(this.pacients);

    this.ps.Update.subscribe(value => {
      this.pacients = this.ps.getPacients()
      this.displayedData = new MatTableDataSource(this.pacients)
    });

  }

  OnAge(datelindja) {
    let age = (new Date().getFullYear() - new Date(datelindja).getFullYear());
    return age;
  }
  onSelected(pacient: Pacient) {
    this.pacientSelected.emit(pacient);
  }
  onSelectedPacient(pacient: Pacient) {
    this.selectedPacient = pacient;
    this.onSelected(pacient);
  }
  onCleared() {
    this.selectedPacient = null;
  }
  onDelete() {
    this.ps.deletePacient(this.selectedPacient);
    this.ps.deleteData();
    this.router.navigate(['/pacientet']);
  }
  ToAddReceta() {
    this.rs.addItems(this.selectedPacient.receta);
    this.router.navigate(['/recetat']);

  }
}
export interface Pacient {
  emri_pacientit: string;
  rraca: string;
  data: string;
  age: string[];
  emri_pronarit: string;
  shenime: string;
  receta: Receta[];
}



