import { Component, Input, Output } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Pacient } from '../pacient';
import { PacientetService } from '../pacientet.service';
import { EventEmitter } from '@angular/core';


@Component({
  selector: 'app-input-pacienti',
  templateUrl: './input-pacienti.component.html',
  styleUrls: ['./input-pacienti.component.css']
})
export class InputPacientiComponent {
  @Input() pacient: Pacient;
  @Output() cleared = new EventEmitter();
  isAdd = true;
  constructor(private ps: PacientetService) { }

  onSubmitt(pacient: Pacient) {
    let newPacient = new Pacient(
      pacient.emri_pacientit,
       pacient.rraca,
        pacient.data,
         pacient.age, 
         pacient.emri_pronarit, 
         pacient.shenime, 
         pacient.receta);
    if (!this.isAdd) {
      this.ps.editPacient(this.pacient, newPacient);
      this.ps.storeData().subscribe(
        data => console.log(data),
        error => console.error(error)
      );
      } else {
      this.pacient = newPacient;
      this.ps.addPacient(this.pacient);
      this.ps.storeData().subscribe(
        data => console.log(data),
        error => console.error(error)
      );
      //console.log(pacient)
    }
  }
  reset() {
    this.isAdd = true;
    this.cleared.emit(null);
  }
  ngOnChanges(changes) {
    if (changes.pacient.currentValue === null) {
      this.isAdd = true;
      this.pacient = { emri_pacientit: null, rraca: null, data: null, age: null, emri_pronarit: null, shenime: null, receta: null };
    } else {
      this.isAdd = false;
    }
  }
}




