import { Component, OnInit, Input, OnDestroy, OnChanges} from '@angular/core';
import { Receta } from '../receta';
import { RecetaService } from '../receta.service';
import { FormArray, FormControl, FormsModule, Validators, FormBuilder, FormGroup} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Pacient } from '/app-animals/src/app/pacientet/pacient';
import { Observable } from 'rxjs/Rx';


@Component({
  selector: 'app-input-receta',
  templateUrl: './input-receta.component.html',
  styleUrls: ['./input-receta.component.css']
})
export class InputRecetaComponent implements OnChanges {
  MyForm: FormGroup;
  private recetaIndex: number;
  private pacient: Pacient;
  private isNew = true;
  @Input() item: Receta;
  isAdd = true;

  constructor(private route: ActivatedRoute,
    private recetaService: RecetaService,
    private formBuilder: FormBuilder,
    private router: Router) {

    this.MyForm = new FormGroup({
      'emri_pacientit': new FormControl(),
      'data_lindjes': new FormControl(),
      'diagnoza': new FormControl(),
      'ilacet': formBuilder.array(['Vendos Ilacet'])
    });
  }
  onAddItem() {
    (<FormArray>this.MyForm.get('ilacet')).push(new FormControl('', Validators.required));
  }


  onRuaj() {
    var newReceta = this.MyForm.value;

    if (this.isNew) {
      this.recetaService.AddReceta(newReceta);

    }
  }
  ngOnChanges(changes) {
    if (changes.item.currentValue === null) {
      this.isAdd = true;
      this.item = { emri_pacientit: null, data_lindjes: null, diagnoza: null, ilacet: null };
      console.log(this.item)
    } else {
      this.isAdd = false;
    }
  }

}



