import { FilterPipe } from './pacientet/filter.pipe';
import { ListaPacientMatComponent } from './pacientet/lista-pacient-mat/lista-pacient-mat.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    HeaderComponent,
    DropdownDirective,
    PacientetComponent,
    RecetaComponent,
    ListaPacientetComponent,
    InputPacientiComponent,
    ListaRecetaComponent,
    InputRecetaComponent,
      DetajetPacientitComponent,
    LoginComponent,
    FilterPipe,
    ListaPacientMatComponent ],
    
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpModule,
    routing,
    ReactiveFormsModule,
    MaterialModule
  ],
  exports:
  [ReactiveFormsModule,
    BrowserModule],
  providers: [RecetaService, PacientetService, HeaderService],
  bootstrap: [AppComponent]
})
export class AppModule { }



