import { Component, OnInit, EventEmitter, Input } from '@angular/core';
import { routing } from '/app-animals/src/app/routes';
import { Pacient } from '../pacient';
import { PacientetService } from '../pacientet.service';
import { Output } from '@angular/core';
import { RecetaService } from 'app/receta/receta.service';
import { FilterPipe } from '../filter.pipe';


@Component({
  selector: 'app-lista-pacientet',
  templateUrl: './lista-pacientet.component.html'
})
export class ListaPacientetComponent implements OnInit {
  pacients: Pacient[] = [];
  @Input() pacient: Pacient;
  // @Input() pacientId: number = 0;
  @Input() selectedPacient: Pacient = null;
  @Output() pacientSelected = new EventEmitter<Pacient>();
  constructor(private ps: PacientetService, private rs: RecetaService) {
    this.pacients = this.ps.getPacients();
  }
  ngOnInit() {
    this.pacients = this.ps.getPacients();
  }
  onSelected(pacient: Pacient) {
    this.pacientSelected.emit(pacient);
  }
  onSelectedPacient(pacient: Pacient) {
    this.selectedPacient = pacient;
    this.onSelected(pacient);
  }
  OnAge(datelindja) {
    let age = (new Date().getFullYear() - new Date(datelindja).getFullYear());
    return age;
  }
  onCleared() {
    this.selectedPacient = null;
  }
}






