import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { InputRecetaComponent } from '../input-receta/input-receta.component';
import { Receta } from '../receta';
import { RecetaService } from '../receta.service';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';

import { Output } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { Pacient } from 'app/pacientet/pacient';


@Component({
  selector: 'app-lista-receta',
  templateUrl: './lista-receta.component.html',
  styleUrls: ['./lista-receta.component.css']
})
export class ListaRecetaComponent implements OnInit {
  items: Receta[] = [];
  selectedPacient: Pacient;

  @Output() recetaSelected = new EventEmitter<Receta>();
  constructor(private rs: RecetaService) { }
  ngOnInit() {
    this.items = this.rs.getItems();
  }
  onSelectItem(item: Receta) {
    this.recetaSelected.emit(item);
    
    
  }
  today = Date.now();


}



